import { signInWithEmailAndPassword } from 'firebase/auth';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import {auth} from './FirebaseDB';

export default function Signin() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState("");

    function login(e) {
        e.preventDefault();
        signInWithEmailAndPassword(auth, email, password)
            .then((user) => {
                setError("");
                setEmail("");
                setPassword("");
            }).catch((erorr) => console.log('Неправильный логин'))
    }
    return (
        <div className='auth'>
            <h3 className='auth_title'>Войдите</h3>
            <form className='auth_wrapper' onClick={login}>
                <input
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    type="email"
                    className="search"
                    placeholder='E-mail'
                />
                <input
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    type="password"
                    className="search"
                    placeholder='Пароль' />
                <button className='btn'>Войти</button>
            </form>

            <p>Еще нет Аккаунта? <Link to='/register' className="bread_links">Зарегистрироваться</Link></p>
        </div>
    )
}
