import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Modal from './Modal';
import { get, ref, update } from 'firebase/database';
import { db } from './Auth/FirebaseDB';
import { useSpring, animated } from '@react-spring/web';

export default function Basket(props) {

   
    const [modalActive, modalSetActive] = useState(false);

    const carts = props.carts.slice().reverse();

    

    useEffect(() => {
        props.total();
    }, [])

    async function handleIncrement(i, count, plus) {
        const userCarts = ref(db, `users/${props.authUid}/carts`)

        try {
            const userFavoriteSnapshot = await get(userCarts);

            const userCart = userFavoriteSnapshot.val();
         
            const keyId = Object.keys(userCart).find(key => userCart[key].id === i.id)
         
            if (keyId) {
                const userIncrement = ref(db, `users/${props.authUid}/carts/${keyId}`)
                if (plus) {
                    update(userIncrement, { count: ++i.count })
                } else if (i.count > 1) {
                    update(userIncrement, { count: --i.count })
                }
                props.total();
            }
        } catch (error) {
            console.error(error.message);
        }
    }

    function format(current) {
        return current.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1 ");
    }

    const AnimatedNumber = ({ number }) => {
        const numberValue = useSpring({
            from: { number: props.prevSum },
            to: { number: number },
            config: { duration: 200 },
        });

        return (
            <animated.span>
                {numberValue.number.to((number) =>
                    Math.round(number).toLocaleString()
                )}
            </animated.span>
        );
    }

    return (
        <>
            <div className="container">
                <div className="basket">
                    <div className={!props.carts.length ? 'basket_items center' : ' basket_items '}>
                        {!props.carts.length ? <><h3 className="title">Корзина пуста</h3>
                            <Link className="card_title" to={'/shoes'}>Перейте в католог</Link></> : carts.map(i => (
                                <div className="basket_item" key={i.id}>
                                    <div className="basket_picture"><img src={`shoes${i.src[0]}`} alt={i.name} /></div>
                                    <div className="basket_item_right">
                                        <div className="basket_mTitle">
                                            <Link to={`/shoes/${i.id}`}>
                                                <div className="card_title">{i.name}</div>
                                            </Link>
                                            <div className="basket_size">
                                                <span>Размер</span>
                                                <div className="card_size">{i.selectSize}</div>
                                            </div>

                                        </div>
                                        <div className="basket_functinol">
                                            <button className="decrement" onClick={() => handleIncrement(i, i.count)}>-</button>
                                            <div className="count">{i.count}</div>
                                            <button className="increment" onClick={() => handleIncrement(i, i.count, true)}>+</button>
                                        </div>

                                        <div className="basket_bottom">
                                            <p className="card_price">{i.price.toLocaleString()}.</p>
                                            <div className="delete" onClick={() => props.onCart(i, true)}><img src="./img/icons/trash.svg" /></div>

                                        </div>

                                    </div>

                                </div>
                            ))}

                    </div>
                    {!props.carts.length ? <></>
                        : <div className="basket_info_block">
                            <h4 className="basket_info_title">Оплата после примерки</h4>
                            <span>Товары, {props.carts.length} шт.</span>
                            <div className="basket_info_total">
                                <div className="bst">Итого</div>
                                <div className="all_price">
               
                                    <AnimatedNumber number={props.sum} />
                                    ₽</div>
                            </div>

                            <div className={props.carts ? 'btn' : 'btn no_active'} onClick={() => modalSetActive(!modalActive)}>Заказать</div>
                            <Modal active={modalActive} setActive={modalSetActive}>
                                <img src="./img/icons/errors.svg" />
                                <p className='errors_text'>Временно недоступно</p>
                            </Modal>
                            <div className="basket_info_polity">Соглашаюсь <span>с правилами пользования торговой площадкой</span> и возврата</div>
                        </div>}
                </div>
            </div >
        </>

    )
}
