import React, { useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom';
import AuthDetails from './Auth/AuthDetails';

export default function Header(props) {
    const childRef = useRef();
    const inputRef = useRef(null);
    const [currentUser, setCurrent] = useState(null);
    const [isChecked, setIsChecked] = useState(false);
    const [input, setInput] = useState('');
    const [mai, setArray] = useState();
    const [isFocused, setIsFocused] = useState(false);

    let [found, setFound] = useState([]);

    function handleInputChange() {
        setIsChecked(!isChecked);
    };

    const handleUser = (user) => {
        setCurrent(user);
    };

    const handleSignUser = () => {
        childRef.current.userSiginOut();
    };

    const handleBlur = () => {
        setIsFocused(false);
        inputRef.current.blur();
        // setInput('')
    }

    const handleFocus = () => {
        setIsFocused(true);
    };

    useEffect(() => {
        setFound([])
        if (input) {
            props.product.forEach((e) => {
                if (e.name.toLowerCase().toLowerCase().includes(input.toLowerCase())) {
                    setFound(prevState => [...prevState, e]);
                }
            })
        }
        // console.log(found);
        // setArray(found);
    }, [input])

    return (

        <header>
            <AuthDetails
                ref={childRef}
                handleUser={handleUser}
                onUid={props.onUid}
                curUser={currentUser}
            />
            <div className="container">
                <nav>
                    <div className="logo">
                        <img src='img/icons/Logo.svg' />
                    </div>

                    <ul className={!isChecked ? 'header_item' : ' header_item active'}>
                        <li className="header_items">
                            <Link to="/shoes" onClick={handleInputChange} className="header_links">Обувь</Link>
                        </li>
                        <li className="header_items">
                            <a href='https://t.me/shoes_stepa' onClick={handleInputChange} target='_blank' className="header_links">Телеграм</a>
                        </li>
                        <li className="header_items">
                            <Link to="/"
                                // onClick={e => {
                                //     // props.scrollToElement();
                                //     // handleInputChange();
                                // }}
                                onClick={handleInputChange}
                                className="header_links">FaQ</Link>
                        </li>
                        <li className="header_items mobail">
                            <Link to="/favorites" className="header_favorites" onClick={handleInputChange}>
                                <span className='header_links'>Изранное</span>
                                <img src="./img/icons/favorites.svg" alt="favorites" />
                                <span className={props.favorites.length ? 'notification active' : 'notification '}>{props.favorites.length}</span>
                            </Link>
                        </li>
                        <li className="header_items mobail">
                            <Link to="/basket" className="header_basket" onClick={handleInputChange}>
                                <span className='header_links'>Корзина</span>
                                <img src='./img/icons/shopping-cart.svg' alt='shopping-cart' />
                                <span className={props.carts.length ? 'notification active' : 'notification '}>{props.carts.length}</span>
                            </Link>
                        </li>

                        <li className="header_items mobail">
                            {currentUser ? (
                                <Link to="/" className="header_user_options"
                                    onClick={handleSignUser}>
                                    <span className='header_links'>Выйти</span>
                                    <img src="./img/icons/log-out.svg" alt="" />
                                </Link>) :

                                <Link to="/login" className="header_user_options" onClick={handleInputChange}>
                                    <span className='header_links'>Войти</span><img src="./img/icons/log-in.svg" alt="" />
                                </Link>
                            }
                        </li>
                    </ul>

                    <div className={!isChecked ? 'header_functional' : 'header_functional active'}>
                        <div className='header_search'>
                            <input
                                type="text"
                                className={isFocused ? 'search active' : 'search'}
                                placeholder="Поиск"
                                ref={inputRef}
                                value={input}
                                onChange={(e) => setInput(e.target.value)}
                                onFocus={handleFocus}
                                onBlur={handleBlur} />
                            <img onClick={handleFocus}
                            src="./img/icons/search.svg" alt="search" className='search_img'/>
                            <div className={isFocused ? 'founder active' : 'founder'}>
                                {found.length ? found.map((e) => (
                                    <Link to={`/shoes/${e.id}`} key={e.id}  onClick={(e) => setInput(null)}
                                        className='founder_item'><img src={`shoes${e.src[0]}`} alt={e.name} />
                                        <div className="founder_left">
                                            <h4 className="founder_title card_title">{e.name}</h4>
                                            <p className="price">{e.price}</p>
                                        </div>
                                    </Link>)) : <span>Ничего не нашлось</span>
                                }   
                            </div>
                        </div>

                        <Link to="/favorites" className="header_favorites" >
                            <img src="./img/icons/favorites.svg" alt="" />
                            <span className={props.favorites.length ? 'notification active' : 'notification '}>{props.favorites.length}</span>
                        </Link>
                        <Link to="/basket" className="header_basket" >
                            <img src='./img/icons/shopping-cart.svg' />
                            <span className={props.carts.length ? 'notification active' : 'notification '}>{props.carts.length}</span>
                        </Link>

                        {currentUser ? (
                            <div className="header_user" >
                                <img src='./img/icons/user.svg' />
                                <Link to="/" className="header_user_options" onClick={handleSignUser}><img src="./img/icons/log-out.svg" alt="" /> <span>Выйти</span></Link>
                            </div>
                        ) : <div className="header_user" >
                            <img src='./img/icons/user.svg' />
                            <Link to="/login" className="header_user_options"><img src="./img/icons/log-in.svg" alt="" /> <span>Войти</span></Link>
                        </div>}

                    </div>

                    <div className="menu back menu--3" onChange={handleInputChange}>
                        <label className="burger">
                            <input type="checkbox" defaultChecked={isChecked} checked={isChecked} />
                            <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="50" cy="50" r="30" />
                                <path className="line--1" d="M0 40h62c18 0 18-20-17 5L31 55" />
                                <path className="line--2" d="M0 50h80" />
                                <path className="line--3" d="M0 60h62c18 0 18 20-17-5L31 45" />
                            </svg>
                        </label>
                    </div>
                </nav>
            </div>
        </header>
    )
}
