import React, { useState } from 'react'
import Accordion from './Accordion'

export default function Faq(props) {
    const data = [
        { id: 1, Titel: 'Какие способы оплаты вы принимаете?', descript: "- Мы принимаем оплату банковскими картами, а также через электронные платежные системы." },
        { id: 2, Titel: 'Как можно сделать заказ?', descript: "Заказ вы можете оформить в своей корзине или написать  нашему менеджеру напрямую в Телеграмм." },
        { id: 3, Titel: 'Что делать, если обувь не подошла по размеру ?', descript: "- У нас есть удобная система возврата и обмена товара, которая позволяет вернуть или обменять товар в течение 14 дней." },
        { id: 4, Titel: 'Как быстро осуществляется доставка вашей продукции?', descript: "- Мы стараемся осуществлять доставку нашей продукции в кратчайшие сроки, обычно это занимает от 2 до 5 дней, в зависимости от места доставки." },
    ];

    const [toggle, setToggle] = useState(false);

    const toggleState = () => {
        setToggle(!toggle);
    }

    const [activeIndex, setActiveIndex] = useState(null);

    const handleToggle = (index) => {
        setActiveIndex(index);
    }


  


    return (
        <div className="container">
            <h2 className="title" ref={props.elementRef}>FaQ</h2>
            <div className="accordion_main">
                {data.map((item, index) => (
                    <Accordion key={index}
                        index={index}
                        Titel={item.Titel}
                        descript={item.descript}
                        activeIndex={activeIndex}
                        onToggle={handleToggle} />


                ))}
            </div>
        </div>
    )
}
