import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Breadcrumb from './Breadcrumb';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

export default function ProductPage(props) {
    const { id } = useParams();

    const settings = {
        dots: false,
        arrows: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        responsive: [
            {
              breakpoint: 768, // маленький экран
              settings: {
                arrows: false, // скрыть стрелки
              },
            },
        ]
    };
    // console.log(props.userData);
    let product = props.userData.find((product) => product.id === Number(id));
    // let productsArray = Object.values(props.userData).flat().filter((product) => product.id === Number(id));
    // console.log(productsArray);

    const descript = Object.keys(product.description).map(key => product.description[key])
    // console.log(descript);
    // console.log(typeof (descript));

    function hasProduct(mas) { //Передаем массив Favorites or Carts
        return Object.keys(mas)
            .some(key => mas[key].id === product.id);
    }

    const mar = Object.keys(props.carts).map(key => {
        if (props.carts[key].id === product.id) {
            return props.carts[key].selectSize;
        };
    })
    // console.log(mar);
    const sedSize = mar.forEach(e => e !== 'undefined')
    // console.log(sedSize);

    // console.log(product);

    useEffect(() => {
        setSrc(product.src[0])
    }, [product]);

    const [src, setSrc] = useState(product.src[0]);

    const [selectedSize, setSelectedSize] = useState();
    // console.log(props.carts);
    // const paths = [
    //     { label: "Главная", url: "/" },
    //     { label: `${product.name}`, url: `/shoes/${id}` },
    // ];

    const clickSize = (product) => {
        product.selectSize = selectedSize;
        if (hasProduct(props.carts)) {
            props.onCart(product)
            setSelectedSize(null)
        } else if (!selectedSize) {
            alert('Выбери размер');
            console.log(selectedSize);
        } else {
            props.onCart(product)
        }

    }
    // const clickSize = (id) => {
    //     selectedSize ? props.onCart(id) : alert('Выбери размер');

    //     const sizesItems = JSON.parse(localStorage.getItem('sizesItems')) || [];
    //     const existingItemIndex = sizesItems.findIndex(item => item.id === id);
    //     if (existingItemIndex !== -1) {
    //         // если товар уже есть в корзине, обновляем его количество
    //         sizesItems.splice(existingItemIndex, 1);
    //     } else {
    //         // если товара еще нет в корзине, добавляем его
    //         sizesItems.push({ id, selectedSize });
    //     }
    //     localStorage.setItem('sizesItems', JSON.stringify(sizesItems))
    // }

    return (
        <div className="container">
            {/* <Breadcrumb paths={paths} /> */}
            <div className="card">
                <div className="card_box">
                    <img src={`.${src}`} className="card_box_img" alt={product.name} />
                    <Slider className="card_galerey" {...settings}>
                        {product.src.map((e, i) => (
                            <img
                                key={e}
                                src={`.${e}`}
                                alt={product.name}
                                onClick={() => setSrc(product.src[i])} />
                        ))}
                    </Slider>

                </div>

                <div className="card_descript">
                    <h3 className="card_title">{product.name}</h3>
                    <p className="card_price mar">{product.price.toLocaleString()}.</p>
                    <div className="card_sizes">
                        {product.sizes.map(e => (
                            <div key={e} className={selectedSize === e ? 'card_size active' : 'card_size '}
                                onClick={() => setSelectedSize(e)} >{e}</div>
                        ))}
                    </div>
                    <div className="card_flex">
                        <div onClick={() => clickSize(product)} className={hasProduct(props.carts) ? "btn active" : "btn "} >
                            {!hasProduct(props.carts) ? "Добавить в корзину" : "Удалить из корзины"}</div>
                        <div className={hasProduct(props.favorites) ? 'favorites add_favorites active' : 'favorites add_favorites'}
                            onClick={(event) => props.onAddFavorite(product, event)}>
                            <svg width="39" height="39" viewBox="0 0 39 39" fill="auto" xmlns="http://www.w3.org/2000/svg">
                                <mask id="mask0_38_211" style={{ maskType: 'alpha' }} maskUnits="userSpaceOnUse" x="0" y="0" width="39" height="39">
                                    <rect width="39" height="39" fill="auto" />
                                </mask>
                                <g mask="url(#mask0_38_211)">
                                    <path d="M19.5 34.125L17.1438 32.0125C14.4083 29.5479 12.1469 27.4218 10.3594 25.6343C8.57187 23.8468 7.15 22.2422 6.09375 20.8203C5.0375 19.3984 4.29948 18.0916 3.87969 16.9C3.4599 15.7083 3.25 14.4895 3.25 13.2437C3.25 10.6979 4.10312 8.57184 5.80938 6.86559C7.51563 5.15934 9.64167 4.30621 12.1875 4.30621C13.5958 4.30621 14.9365 4.60413 16.2094 5.19996C17.4823 5.7958 18.5792 6.63538 19.5 7.71871C20.4208 6.63538 21.5177 5.7958 22.7906 5.19996C24.0635 4.60413 25.4042 4.30621 26.8125 4.30621C29.3583 4.30621 31.4844 5.15934 33.1906 6.86559C34.8969 8.57184 35.75 10.6979 35.75 13.2437C35.75 14.4895 35.5401 15.7083 35.1203 16.9C34.7005 18.0916 33.9625 19.3984 32.9062 20.8203C31.85 22.2422 30.4281 23.8468 28.6406 25.6343C26.8531 27.4218 24.5917 29.5479 21.8562 32.0125L19.5 34.125Z" fill="auto" />
                                </g>
                            </svg>

                        </div>
                    </div>
                    <h5 className="sub_descript">ОПИСАНИЕ</h5>

                    <div > {product.description.map(e => (
                        <p className="descript" key={e}>{e}</p>
                    ))}</div>
                </div>
            </div>

        </div >
    )
}

