import React from 'react'

export default function Breadcrumb({ paths }) {
    return (

        <ul className='breadcrumb'>
            {paths.map((path, index) => (
                <li className='bread_text' key={index}>
                    <a className='bread_links' href={path.url}>{path.label}</a>
                </li>
            ))}
        </ul>

    );
}
