import React, { useState } from 'react'
import { Link } from 'react-router-dom';
import { ref, set } from "firebase/database";
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { auth, db } from './FirebaseDB';


export default function Signup(props) {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [copyPassword, setCopyPassword] = useState("")
    const [error, setError] = useState("");

    function register(e) {
        e.preventDefault();
        if (copyPassword !== password) {
            setError('Пароли не совподают')
            return
        }
        createUserWithEmailAndPassword(auth, email, password)
            .then((user) => {
                const userUid = ref(db, `users/${auth.currentUser.uid}`)
                props.onUid(auth.currentUser.uid)
                set(userUid, true)
                setError("");
                setEmail("");
                setCopyPassword("");
                setPassword("");
            }).catch((error) => console.log(error))
    }

    return (
        <div className='auth'>
            <h3 className='auth_title'>Зарегистрироваться</h3>
            <form className='auth_wrapper' onClick={register}>
                <input
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    type="email"
                    className="search"
                    placeholder='E-mail'
                />
                <input
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    type="password"
                    className="search"
                    placeholder='Пароль' />
                <input
                    value={copyPassword}
                    onChange={(e) => setCopyPassword(e.target.value)}
                    type="password"
                    className="search"
                    placeholder='Подтвердите пароль' />
                <button className='btn'>Войти</button>
            </form>

            <p>Есть Аккаунт? <Link to='/login' className="bread_links">Войдите</Link></p>
        </div>
    )
}
