import React, { useEffect, useRef, useState } from 'react'

export default function Accordion(props) {
    const refHeight = useRef();
    useEffect(() => {
        setHeight(`${refHeight.current.scrollHeight+40}px`)
    }, [])

    const [height, setHeight] = useState();
    const toggle = props.activeIndex === props.index;
   
    const handleToggle = () => {
        props.onToggle(props.index);
    }

    return (
        <div className={toggle ? "accordion_content active" : "accordion_content"} onClick={handleToggle}>
            <h4 className={toggle ? "accordion_title active" : "accordion_title"} >{props.Titel}</h4>
            <p className={toggle ? "accordion_descript active" : "accordion_descript"}
                style={{ height: toggle ? `${height}` : "0px" }}
                ref={refHeight}>
                {props.descript}
            </p>
            <hr />
        </div>
    )
}
