import React from 'react'
import { Link } from 'react-router-dom';

export default function Favorites(props) {
    // let god = props.dates.filter(i => props.favorites.includes(i.id));
    return (
        <>
            <div className="container">
                <div className="favorites_items">
                    {!props.favorites.length ? <h3 className="title">Корзина пуста</h3> : props.favorites.reverse().map(e => (
                        <Link to={`/shoes/${e.id}`} className="favorites_item" key={e.id}>
                            <div className="favorites_top">
                                <img  src={`shoes${e.src[0]}`} alt="" />
                                <div className="card_price">{e.price.toLocaleString()}.</div>
                            </div>
                            <div className="favorites_bottom">
                                <h3 className="card_title">{e.name}</h3>
                                <div className='favorites add_favorites active'
                                    onClick={(event) => { event.preventDefault(); props.onAddFavorite(e, event) }}>

                                    <svg width="39" height="39" viewBox="0 0 39 39" fill="auto" xmlns="http://www.w3.org/2000/svg">
                                        <mask id="mask0_38_211" style={{ maskType: 'alpha' }} maskUnits="userSpaceOnUse" x="0" y="0" width="39" height="39">
                                            <rect width="39" height="39" fill="auto" />
                                        </mask>
                                        <g mask="url(#mask0_38_211)">
                                            <path d="M19.5 34.125L17.1438 32.0125C14.4083 29.5479 12.1469 27.4218 10.3594 25.6343C8.57187 23.8468 7.15 22.2422 6.09375 20.8203C5.0375 19.3984 4.29948 18.0916 3.87969 16.9C3.4599 15.7083 3.25 14.4895 3.25 13.2437C3.25 10.6979 4.10312 8.57184 5.80938 6.86559C7.51563 5.15934 9.64167 4.30621 12.1875 4.30621C13.5958 4.30621 14.9365 4.60413 16.2094 5.19996C17.4823 5.7958 18.5792 6.63538 19.5 7.71871C20.4208 6.63538 21.5177 5.7958 22.7906 5.19996C24.0635 4.60413 25.4042 4.30621 26.8125 4.30621C29.3583 4.30621 31.4844 5.15934 33.1906 6.86559C34.8969 8.57184 35.75 10.6979 35.75 13.2437C35.75 14.4895 35.5401 15.7083 35.1203 16.9C34.7005 18.0916 33.9625 19.3984 32.9062 20.8203C31.85 22.2422 30.4281 23.8468 28.6406 25.6343C26.8531 27.4218 24.5917 29.5479 21.8562 32.0125L19.5 34.125Z" fill="auto" />
                                        </g>
                                    </svg></div>
                            </div>

                        </Link>

                    ))}
                </div>

            </div>
        </>
    )
}
